import { Component, OnInit } from '@angular/core';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';

import * as pluginDataLabels from 'chartjs-plugin-datalabels';

import LandingData from '../../assets/test.json';

import { Legend } from './legend';

@Component({
  selector: 'app-bubble-chart',
  templateUrl: './bubble-chart.component.html',
  styleUrls: ['./bubble-chart.component.css']
})
export class BubbleChartComponent implements OnInit {
  public bubbleChartOptions: ChartOptions = {
    responsive: false,
    animation: { duration: 0 },
    legend: { display: false, position: 'top', },
    scales: {
      xAxes: [ { display: false, ticks: { min: 0, max: 2048, padding: 0, } } ],
      yAxes: [ { display: false, ticks: { min: 0, max: 2048, padding: 0, } } ],
    },
    tooltips: {
      callbacks: {
        label: (tooltipItem, data) => {
          let lab: string;
          lab = ' (' + (tooltipItem.datasetIndex + 1).toString() + ')  '
          + data.datasets[tooltipItem.datasetIndex].label;
          if (data.datasets[tooltipItem.datasetIndex].data.length > 1) {
            lab = lab + ' - Game ' + (tooltipItem.index + 1).toString();
          }
          return lab;
        }
      }
    },
    plugins: {
      datalabels: {
        color: 'black',
        font: {
          size: 7,
          weight: 'bold',
        },
        formatter: (value, context) => (context.datasetIndex + 1),
      }
    }
  };

  public bubleChartPlugins = [pluginDataLabels];

  public bubbleChartType: ChartType = 'bubble';
  public bubbleChartLegend = true;

  public bubbleChartData: ChartDataSets[];

  public gamesList: string[];
  public legends: Legend[];

  constructor() { }

  ngOnInit() {
    this.bubbleChartData = JSON.parse(JSON.stringify(LandingData));
    this.gamesList = this.getGamesList();
    this.legends = this.getLegendsList();
  }

  private getGamesList(): string[] {
    const result: string[] = [];
    let numGames = 0;
    this.bubbleChartData.forEach(dataset => {
      if (dataset.data && Array.isArray(dataset.data)) {
        numGames = numGames >= dataset.data.length ? numGames : dataset.data.length;
      }
    });
    for (let i = 0; i < numGames; i++) {
      result.push('Game ' + (i + 1));
    }
    return result;
  }

  private getLegendsList(): Legend[] {
    const result: Legend[] = [];
    this.bubbleChartData.forEach((dataset, index) => {
      if (dataset.backgroundColor && dataset.label) {
        const obj: Legend = {
          color: '',
          name: '',
          index: 0
        };
        if (typeof(dataset.backgroundColor) === 'string') {
          obj.color = dataset.backgroundColor;
        } else {
          obj.color = 'black';
        }
        obj.name = dataset.label;
        obj.index = index + 1;
        result.push(obj);
      }
    });
    return result;
  }

  public changeData(index: number): void {
    if (index === 0) {
      this.bubbleChartData = JSON.parse(JSON.stringify(LandingData));
    } else {
      const clone = JSON.parse(JSON.stringify(LandingData));
      LandingData.forEach((elt, idx) => {
        if (elt.data.length >= index) {
          const temp = [];
          temp.push(elt.data[index - 1]);
          clone[idx].data = temp;
        } else {
          clone[idx].data = [];
        }
      });
      this.bubbleChartData = clone;
    }
  }
}
